<template>
  <div>
    <h2>Detail page for {{ response.title }}</h2>
    <tbody>
    <tr>
      <th>Title</th>
      <td>{{ response.title }}</td>
    </tr>
    <tr>
      <th>Date</th>
      <td>{{ response.date }}</td>
    </tr>
    <tr>
      <th>Location</th>
      <td>{{ response.location }}</td>
    </tr>
    <tr>
      <th>Public description</th>
      <td>{{ response.publicdescription }}</td>
    </tr>
    <tr>
      <th>Private Description</th>
      <td>{{ response.privatedescription }}</td>
    </tr>
    </tbody>
    <h2>Equipment</h2>
    <tbody>
    <tr>
      <th>
        Title
      </th>
      <th>
        Description
      </th>
    </tr>
    <!-- eslint-disable-next-line -->
    <tr v-for="equipment in response.equipmentById">
      <td>
        {{ equipment.title }}
      </td>
      <td>
        {{ equipment.description }}
      </td>
    </tr>
    </tbody>
    <h2>Helpers</h2>
    <tbody>
    <tr>
      <th>
        Title
      </th>
      <th>
        Description
      </th>
    </tr>
    <!-- eslint-disable-next-line -->
    <tr v-for="helper in response.helpersById">
      <td>
        {{ helper.title }}
      </td>
      <td>
        {{ helper.description }}
      </td>
    </tr>
    </tbody>
  </div>
</template>
<script>
import AXIOS from '../../../config/http-commons';

export default {
  data() {
    return {
      response: [],
      errors: [],
      loading: false,
    };
  },

  created() {
    this.loading = true;
    // eslint-disable-next-line
      AXIOS.get('http://ppr.surreal.is:8080/rest/operation/' + this.$route.params.id)
      .then((response) => {
        this.response = response.data;
        this.loading = false;
      })
      .catch(() => {
        this.loading = false;
      });
  },
};
</script>



