<template>
  <div>
    <h2>User details for user {{ $route.params.id }}</h2>
      <tbody>
      <tr>
        <th>First Name</th>
        <td>{{ response.firstname }}</td>
      </tr>
      <tr>
        <th>Last Name</th>
        <td>{{ response.lastname }}</td>
      </tr>
      <tr>
        <th>Username</th>
        <td>{{ response.id }}</td>
      </tr>
      <tr>
        <th>Email address</th>
        <td>{{ response.id }}</td>
      </tr>
      <tr>
        <th>Birthday</th>
        <td>{{ response.birthday }}</td>
      </tr>
      </tbody>
  </div>
</template>
<script>
import AXIOS from '../../../config/http-commons';

export default {
  data() {
    return {
      response: [],
      errors: [],
      loading: false,
    };
  },

  created() {
    this.loading = true;
    // eslint-disable-next-line
        AXIOS.get('http://ppr.surreal.is:8080/rest/userdetails/' + this.$route.params.id)
      .then((response) => {
        this.response = response.data;
        this.loading = false;
      })
      .catch(() => {
        this.loading = false;
      });
  },
};
</script>



