<template>
  <div>
    <p v-if="errors.length">
      <b>Please correct the following error(s):</b>
    <ul class="error-list">
      <!-- eslint-disable-next-line -->
      <li class="error-list-item" v-for="error in errors">{{ error }}</li>
    </ul>
    </p>
    <p>
      <label for="username">User:</label>
      <input id="username" v-model="login.username" placeholder="Username">
    </p>
    <p>
      <label for="password">Password:</label>
      <input id="password" type="password" v-model="login.password">
    </p>
    <p>
      <button v-on:click="checkForm">Submit</button>
    </p>
  </div>
</template>

<script>
import AXIOS from '../../../config/http-commons';

export default {
  data() {
    return {
      errors: [],
      login: {
        username: null,
        password: null,
      },
    };
  },
  methods: {
    checkForm(e) {
      if (this.login.username && this.login.password) {
        const bodyFormData = new FormData();
        bodyFormData.set('username', this.login.username);
        bodyFormData.set('password', this.login.password);
        AXIOS({
          method: 'post',
          url: 'http://ppr.surreal.is:8080/public/users/register/',
          data: bodyFormData,
          config: { headers: { 'Content-Type': 'application/x-www-form-urlencoded' } },
        })
          .then((token) => {
            AXIOS.get('http://ppr.surreal.is:8080/users/current/', { headers: { Authorization: `Bearer ${token.data}` } })
              .then((response) => {
                this.$store.dispatch('login', response.data)
                  .then(() => {
                    this.$router.push({ name: 'Home' });
                  });
              });
          });
        return true;
      }
      this.errors = [];
      if (!this.login.username) this.errors.push('Username required.');
      if (!this.login.password) this.errors.push('Password required.');
      e.preventDefault();
      return false;
    },
  },
};
</script>



